.Toolbar {
    height: 72px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #343A40;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    height: 80%;
    display: contents;
    color:white;
}
.search{
    color: #FFFFFF;
}
@media (max-width: 992px) {
    .DesktopOnly {
        display: none;
    }
}
@media (min-width:992px){
    .MobileOnly{
        display: none;
    }
}